import { Button as ButtonComponent, ButtonSize, ButtonColor } from 'im.v2.component.elements';
import { SupervisorBaseMessage } from 'im.v2.component.message.supervisor.base';

import './css/supervisor-tariff-expansion.css';

// @vue/component
export const SupervisorTariffExpansionMessage = {
	name: 'SupervisorTariffExpansionMessage',
	components: { ButtonComponent, SupervisorBaseMessage },
	props: {
		item: {
			type: Object,
			required: true,
		},
		dialogId: {
			type: String,
			required: true,
		},
	},
	data(): {showAddToChatPopup: boolean}
	{
		return {
			showAddToChatPopup: false,
		};
	},
	computed:
	{
		ButtonSize: () => ButtonSize,
		ButtonColor: () => ButtonColor,
	},
	methods:
	{
		loc(phraseCode: string, replacements: {[p: string]: string} = {}): string
		{
			return this.$Bitrix.Loc.getMessage(phraseCode, replacements);
		},
	},
	template: `
		<SupervisorBaseMessage
			:item="item"
			:dialogId="dialogId"
			:title="loc('IM_MESSAGE_SUPERVISOR_TARIFF_EXPANSION_TITLE')"
			:description="loc('IM_MESSAGE_SUPERVISOR_TARIFF_EXPANSION_DESCRIPTION')"
		>
			<template #image>
				<div class="bx-im-message-tariff-expansion__image-wrapper" />
			</template>
			<template #actions>
				<ButtonComponent
					:size="ButtonSize.L"
					:isRounded="true"
					:color="ButtonColor.Success"
					:text="loc('IM_MESSAGE_SUPERVISOR_TARIFF_EXPANSION_BUTTON_CHANGE_TARIFF')"
				/>
				<ButtonComponent
					:size="ButtonSize.L"
					:color="ButtonColor.LightBorder"
					:isRounded="true"
					:text="loc('IM_MESSAGE_SUPERVISOR_TARIFF_EXPANSION_BUTTON_MORE_DETAILED')"
				/>
			</template>
		</SupervisorBaseMessage>
	`,
};
